import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    basketCounter : 0,
    basket: []
  },
  mutations: {
    resetBasket (state) {
      state.basketCounter = 0;
      state.basket = [];
    },
    addToBasket(state,item) {
      state.basketCounter++;
      state.basket.push(item);
    },
    delFromBasket(state,i) {
      state.basket.splice(i.i,1);
      state.basketCounter--;
    }
  },
  actions: {
  },
  modules: {
  }
})
